#main {
    position: absolute;
    top: 110px;
    width: 100%;
    min-width: 1024px;
}



/***** package page*****/
#Sec0_package{
    overflow: hidden;
}
#Sec0_package .sect-inner {
    width: 70%;
    position: relative;
    margin: 0 auto;
}

#Sec0_package .sect-inner .mouse-move{
    padding: 80px 0 ;
    display: flex;
    justify-content: center;
    align-items: center;
}

#Sec0_package .sect-inner .mouse-move .mouse{
    width: 60px;
    height: 100px;
    border: 4px solid #ccc;
    border-radius: 30px;
    position: relative;
}

#Sec0_package .sect-inner .mouse-move .mouse::before{
    content: '';
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background: #ccc;
    position: absolute;
    top: 20px;
    left: 18px;
    opacity: 1;
    animation : scroll 1s infinite linear
}

@keyframes scroll {
    0%{opacity: 0; top: 20px;}
    100%{opacity: 1; top: 50px;}
}

#Sec0_package .sect-inner .sect-title{
    text-align: center;
    font-family: "Bebas";
    font-size: 80px;
    font-weight: bold;
    padding-bottom: 30px;
    padding-top: 70px;
    color: #a5a5a5;
    }

#Sec0_package.package .sect-content {
    position: relative;
    height: 570px;
}

#Sec0_package.package .sect-content .work-text{
    left: 6%;
    top: 30%;
    width: 40%;
    position: absolute;
    transform: translateX(-900px);
    transition: 0.7s;
}

#Sec0_package.package .sect-content .work-text h4{
    margin-bottom: 12px;
    padding-left: 40px;
    font-size: 28px;
    position: relative;
}


#Sec0_package.package .sect-content .work-text h4::before{
    content: attr(number);
    font-size: 40px;
    position: absolute;
    left: 0;
    top: 50%; transform: translateY(-50%);
}

#Sec0_package.package .sect-content.active .work-text{
    transform: translateX(0);
}

#Sec0_package.package .sect-content .work-text .btn-wrap{
    padding: 18px 0;
}

#Sec0_package.package .sect-content .work-text .btn-wrap button{
    border: 1px solid #adadad;
    padding: 12px 36px;
}
#Sec0_package.package .sect-content.active .pack-item{
    transform: translateX(0);
}

#Sec0_package.package .sect-content .pack-item{
    right: 3%;
    bottom: 0;
    width: 55%;
    position: absolute;
    transform: translateX(900px);
    transition: 0.7s;
}
#Sec0_package.package #package02 .pack-item{
    width: 50%; right: 5%; bottom: 15%;
}

#Sec0_package.package #package03 .pack-item{
    width: 40%; right: 10%;
}

#Sec0_package.package #package06 .pack-item{
    width: 44%; right: 8%;
    
}


#modal-pack-pc{
    position: fixed;
    top: 0; left: 0; right: 0; bottom : 0;
    /* display: none; */
    background: rgba(0,0,0,0.8);
    z-index: 500;
}

#modal-pack-pc .modal-inner{
    position: absolute;
    width: 100%; height: 100%;
    top: 0; left: 0;
}

#modal-pack-pc .modal-inner .modal-container{
    width: 86%;
    height: 100vh;
    margin: 0 auto;
    overflow-y: scroll;
}
#modal-pack-pc .modal-inner .modal-container .modal-area img{
    display: block;
}

#modal-pack-pc .modal-inner #close-modal-btn{
    position: absolute;
    top: 24px; right: 24px;
    width: 30px; height: 30px;
}

#modal-pack-pc .modal-inner #close-modal-btn span{
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 100%;
    height: 4px;
    background: #fff;
    border-radius: 2px;
    transform-origin: center;
}
#modal-pack-pc .modal-inner #close-modal-btn span:nth-child(1){
    transform: rotate(45deg); 
}
#modal-pack-pc .modal-inner #close-modal-btn span:nth-child(2){
    transform: rotate(-45deg);
}


@media screen and (min-width:1400px){
    #Sec0_package .sect-inner{width: 60%;}
    #Sec0_package.package .sect-content .pack-item{width: 44%; right: 10%;}
}

@media screen and (min-width:1280px){
    #Sec0_package.package .sect-content .work-text{left: 6%;}
    #Sec0_package.package .sect-content .pack-item{right: 6%; width: 50%;}
}